body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
  background-color: #FBAB7E;
  background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
}

ol, ul {
  padding:0;
  margin: 0;
}

li {
  list-style-type: none;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: rgb(56, 173, 177);
}

.board-row {
  display: flex;
  justify-content: center;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 60px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
  cursor: pointer;
}

.square:hover {
  background: rgb(211, 211, 211);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.game-info {
  margin-left: 20px;
}

.menu-item {
  margin-bottom: 10px;
  font-size: 24px;
  cursor: pointer;
}

.menu-item:hover {
  opacity: 0.7;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-item--disabled {
  color: #999;
  pointer-events: none;
}

.choose-mode {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}

.choose-mode-item {
  margin-right: 10px;
}

.buttons-block {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.button {
  width: 33.33%;
  cursor: pointer;
}

.button a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.board-wrap {
  position: relative;
}

.board-result {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  font-size: 20px;
  color: #F7CE68;
}

.board-result.board-result--visible {
  display: flex;
}

.board-result-btn {
  margin-top: 10px;
  cursor: pointer;
}

@media(max-width:360px) {
  .square {
    width: 50px;
    height: 50px;
    font-size: 24px;
  }
}